eir house),\n              [00:06.00] she had gone outside the house (to wash the utensils) carrying two cups.\n              [00:09.00] hers and her daughters' and a cooking bowl with a spatula\n              [00:13.50] Just before that her husband had returned from the shop\n              [00:17.00] and she had served tea to him and her son. While leaving the room she had told\n              [00:22.00] her husband that her uncle and aunt are visiting tomorrow,\n              [00:26.00] she need money to buy some groceries to cook for them.\n              [00:30.00] He told her not to worry and that he will but it himself for them\n              [00:34.00] She agreed, giving him a smile and went about to clean the cups.\n              [00:40.00] and then there was loud bang, we even heard it here.\n              [00:43.00] Everything seemed to be shaking.\n              [00:47.00] I told my family here, firing is going on in Shadi Margh area.\n              [00:50.00] We phoned the children and inquired about it, they said, we don't have any idea.\n              [00:54.00] Maybe it is practice fire, we are siting indoors.\n              [00:59.00] We didn't know what was happening.\n              [01:03.00] That house had a boundary wall made up of tin sheets.\n              [01:00.00] And exactly opposite the courtyard tap there is a big hole in the tin sheet.\n              [01:10.00] That is where the bullet entered and hit her.\n              [01:18.00] This is only what we know, what else we can say.\n              [01:21.00] We have all turned mad here, day and night thinking about her only.\n              [01:28.00] We feel her face; her shadow is right in front of us. Every day and every night.\n              [01:35.00] What do we do now.\n              [01:39.00] Everyone tells us, come what may you cannot get her back.\n              [01:44:00] Her husband is my youngest son\n              [01:50.00] he is suffering from depression now and is on medication.\n              [01:55.00] Every evening when he returns from work,\n              [01:57.50] he keeps looking at her photos on his mobile and keeps crying,\n              [02:00.00] then looks helplessly at his children. What can he do?\n              [02:03.00] they have lost their mother. Everyone needs mother.\n              [02:07.50] I lost mine, I too miss her, one needs parents even when one turns old.\n              [02:15.00] Her daughter was a baby then.\n              [02:20.00] And Firdausa would not let her out of her sight even for a minute.\n              [02:26.00] I try to take care of her now, but she misses her mother.\n              [02:31.00] Her son goes daily to her grave.\n              [02:37.00] And he just sits there staring at the ground.\n              [02:42.00] A neighbor asked me about his whereabouts yesterday\n              [02:47.00] I said that he might be playing around.\n              [02:50.00] The neighbor said, 'No, the boy is at his mother's grave'.\n              [02:56.00] I rushed to graveyard and he was just sitting there alone.\n              [03:02.00] Staring at her grave with a sad look on his face.\n              [03:05.00] I hugged him and I said, I will give you my life, why are you here? Come home.\n              [03:08.00] He said grandma let me be here, you go home.\n              [03:11.00] Then he said, 'Grandma, you think I go out and play around every day?\n              [03:14.00] I like to come here and sit instead'.\n              [03:23.00] She was a different person, beautiful, young and well-mannered.\n              [03:28.00] So civilized and soft-spoken, She was like a pearl.\n              [03:31.00] What do we do now?\n            ")]):(_vm.name == 'Haseeb Ahmad Khan')?_c('div',{ref:"lyrics"},[_vm._v("\n              [00:00.01] Haseeb's Father: I had asked him to join me as my helper,\n              [00:03.00] but he told me, \"Father, I didn't like this driving business.\"\n              [00:06.00] He said this to me clearly when I met him at Kot Bhalwal jail.\n              [00:09.00] I asked him what you will do then.\n              [00:11.00] I don't have 10 or 20 lakh rupees to give you\n              [00:14.00] and say take it and start any business.\n              [00:17.00] On this he regretted, said sorry and assured me\n              [00:21.00] that he would not give me any chance to complain from then on.\n              [00:26.00] He started working as painter, where worked for 4-5 days\n              [00:30.00] and earned about 2000-2500 rupees.\n              [00:33.00] My wife told me he has got this much money\n              [00:36.00] and he is not giving me anything out of it.\n              [00:40.00] I told her don't bother him.\n              [00:43.00] Let him earn for his personnel expenses only, this way\n              [00:46.00] he will be better, and no one will say he is wanderer.\n              [00:53.00] I told her be calm let him spend whatever he earns.\n              [01:00.00] He even told his mother that father has told me to earn just for myself,\n              [01:06.00] don't pay us but to work for myself.\n              [01:09.00] And then Just after 14 days, he died.\n              [01:16.00] He was very affectionate, if any friend would have asked\n              [01:21.00] him to walk to Srinagar from Baramulla, he would do it.\n              [01:25.00] Without using any sort of transport, where one would pay\n              [01:30.00] rupees twenty to a bus and go Srinagar,\n              [01:33.00] but if a friend asked him to do it for them, he would just walk.\n              [01:37.00] We don't have any guarantee of life here.\n              [01:39.00] Not even within the four walls of home.\n              [01:41.00] Whenever I leave for Delhi, Mumbai or Jammu for my work,\n              [01:46.00] all the way I keep thinking about the security of my family\n              [01:50.00] because I have seen my son being killed in front of me.\n              [01:53.00] I am always apprehensive about the lives of my other family\n              [01:56.00] members. They even can kill me, it is not a big thing for them.\n              [01:58.00] We know his killers too, a militant who lives close by.\n              [02:03.00] From his father's shop we brought grocery items for the customary meals\n              [02:08.00] for four days of mourning.\n              [02:15.00] But at that time we were not aware that his son had killed Haseeb\n            ")]):(_vm.name == 'Liyaqat Ahmad Dar')?_c('div',{ref:"lyrics"},[_vm._v("\n              [00:00.05] Mother: He was a student and had just cleared his Class XII exams with very high marks,\n              [00:06.00] but it was not destined. We gave him INR 3000 as he had to buy some books.\n              [00:12.40] He wanted to join a coaching center in the city at Parraypora and by the grace of God\n              [00:18.00] he had qualified the entrance test there in the his very first attempt,\n              [00:26.00] but they told him to join after passing the 12th, class, which he did and\n              [00:30.00] now he was going both the places. He had chosen the tough subjects which he passed in very first year.\n              [00:35.60] When he cleared his exams, they came from Parrypora to congratulate me.\n              [00:40.00] They laureated him with garlands and medals.\n              [00:44.50] He had no idea that some encounter was going on that side and\n              [00:50.00] they (Army) just targeted him firing indiscriminately. They were seven boys,\n              [01:00.00] students just passing by and shot dead.\n              [01:05.00] (Mother starts wailing when a photograph of him is show to us)\n              [01:11.00] Your mother would sacrifice herself for you my son, immolate herself, on your photograph!\n              [01:20.00] You are my backbone, I would sacrifice my eyesight of my being for you, oh my soul, apple of my eye.\n              [01:30.00] Oh, my youth! I would give you my liver, sacrifice my life for your existence.\n              [01:40.00] Parveen would immolate herself for you oh my backer, my son!\n              [01:47.00] I would sacrifice my eyesight for you my son.\n              [01:51.00] I have lost my mental balance my son. I have turned insane. Oh, my son!\n              [02:01.00] What happened to you my son?\n              [02:09.00] You left all your clothes behind here, what do I do with them my son.\n              [02:20.00] All your books, will they go to waste, oh my son!\n              [02:35.00] How will I get you back? How will I find you? Oh, my son!\n              [02:49.00] These clothes you bought for ten thousand rupees and\n              [02:53.00] all those are sprawled around here only. Oh, my son!\n              [02:57.00] Oh my god!\n              [03:04.00] Daughter: Maa let them have the tea. You're not letting them drink their tea.\n              [03:08.00] Mother: Who had any idea that this will happen to me? Mom will immolate herself for you,\n              [03:18.00] for these school bags my son, oh my son.\n              [03:22.00] Daughter: Maa look I had to show it to them that is why I bought it here.\n              [03:28.00] If you keep crying like this, I will never show it (his photograph) to you again.\n              [03:36.00] Mother: He was the best. The best one. Physically fit, not weak. A big helping hand to his father.\n              [03:43.00] He used to study and also help his father in carrying the milk from one place to another.\n              [03:51.00] He was the best one with firm hands and feet.\n              [03:56.00] Please have some more tea.\n            ")]):(_vm.name == 'Mushtaq Ahmad Mir')?_c('div',{ref:"lyrics"},[_vm._v("\n              [00:00.00] Habla Begum (w/o Mustaq Ahmad Mir)\n              [00:01.00] I used to run a tailoring shop at my parents' house,\n              [00:03.00] where I had many workers and he was one among them.\n              [00:05.00] One day he proposed to me and said,\n              [00:07.00] \"Some of my friends have taken government jobs,\n              [00:10.00] a few have opted for jihad, but I want to carry you in my lap\n              [00:13.00] all my life and that would be my way of Jihad\".\n              [00:16.00] I told him, look I am disabled, I cannot marry you\n              [00:28.00] but he said, \"I would go to any extent to care for you\n              [00:20.00] and will carry you on my shoulders all my life.\"\n              [00:22.00] Then we got married, I came here with him.\n              [00:24.00] After two years we had our first child.\n              [00:26.00] At that stage he did everything for me, nursing me.\n              [00:28.00] He even used to change my cloths.\n              [00:30.00] He used to buy new cloths for me with a lot of love too.\n              [00:32.00] He used to tell me that, \"You should not have less number of dresses\n              [00:34.00] than any other woman.\"\n              [00:36.00] These are the dresses these girls took out\n              [00:38.00] from the boxes yesterday, look at them.\n              [00:41.00] He had brought these for me, and I am yet to wear them.\n              [00:44.00] He was always saying that you must dress up nicely,\n              [00:47.00] the way other (normal) women are dressing up.\n              [00:50.00] Disability shouldn't come in your way.\n              [00:53.00] Many people told him that she is physically challenged\n              [00:56.00] and that he should go and get married to someone else,\n              [00:59.00] but he always replied by saying \"Don't talk to me like that,\n              [01:03.00] for me there is no one like her\".\n              [01:06.00] He always pampered me, never misbehaved and was never rude.\n              [01:15.00] He used to tell me that when I die,\n              [01:18.00] he would give me the Ghusl (obligatory bath) first and then only let people\n              [01:22.00] know that I have passed away.\n              [01:23.00] On hearing this I used to cry and tell him, I am physically challenged,\n              [01:26.00] people will pass sarcastic comments.\n              [01:28.00] I never knew that he is going to leave me\n              [01:31.00] leave me forever like this.\n            ")]):(_vm.name == 'Ubaid Manzoor Lone')?_c('div',{ref:"lyrics"},[_vm._v("\n              [00:00.01] Ubaid's Mother: He was studying in 11th standard.\n              [00:02.00] Day before there was rumor that there is Hartal (shutdown)\n              [00:04.00] on that day.\n              [00:06.00] But in the morning it was like a normal day so he along with\n              [00:08.00] his classmates left for the school.\n              [00:11.00] When they reached Baramulla everything including schools were closed,\n              [00:14.00] so they turned back.\n              [00:18.00] On their way back to home few kids were bathing\n              [00:26.00] in a stream ahead of that spot where some youngsters\n              [00:30.00] were pelting stones at the security forces.\n              [00:37.00] Personnel of Special Task Force and the Army were\n              [00:43.00] chasing the boys in same direction.\n              [00:52.00] His classmates ran much faster than him, but he couldn't\n              [00:58.00] because of his physique. He was bulky so he couldn't run fast.\n              [01:03.00] He had such a physique that no one used to believe\n              [01:05.00] that he was just 16 years old.\n              [01:08.00] Even the doctors at city hospital asked us\n              [01:12.00] how many children he has got?\n              [01:14.00] He used to look like an older man.\n              [01:16.00] So he couldn't run, he was just walking, and army fired at him.,\n              [01:21.00] The bullet pierced his heart.\n              [01:26.00] As the news came that Ubaid been injured,\n              [01:29.00] people came screaming in large numbers and\n              [01:32.00] we rushed him to city hospital, where he was in coma for fifteen days.\n              [01:37.00] He was highly studious and a workaholic. He used to take care of household,\n              [01:44.00] was good in his studies and even good at sports.\n              [01:52.00] He used to play Kabaddi as well as many other games.\n              [02:00.00] He represented his school in many sports events.\n              [02:09.00] Before this incident he told us that the school is sending him Jammu\n              [02:15.00] for fifteen days to participate in some tournament.\n              [02:18.00] His father happily gave him three thousand rupees for this but\n              [02:23.00] after some days he suddenly changed his mind and\n              [02:27.00] dropped the idea of participating in that tournament.\n              [02:30.00] Maybe because Allah was waiting for him in heaven.\n              [02:34.00] Before that he had gone for many such sports events.\n              [02:41.00] But this is now just our misfortune.\n              [02:46.00] We used to be very cautious about everything when he was at home.\n              [02:52.00] Because he was very particular about his food and\n              [02:56.00] not like my other son who doesn't mind what food he is being offered.\n              [03:02.00] Whenever he entered the kitchen and smelled that\n              [03:05.00] only vegetarian food like Haakh (Collard greens) is being prepared,\n              [03:07.00] he used to say keep it for yourself today I am not going to have it.\n              [03:10.00] He never liked Haakh and was fond of mutton and yes, he also\n              [03:14.00] used to like lentils and some vegetables like tomato and peas.\n              [03:18.00] He loved eating all that.\n            ")]):_c('div',{ref:"lyrics"},[_vm._v("Error")])])],1)],1)],1)],1)],1)}
